import slugify from 'slugify';
import { shape, string } from 'prop-types';
import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'gatsby';

import { useQueryString } from 'hooks';
import { Page, Filter, Marker, Pagination } from 'components';

const Home = ({ location }) => {
  const querystring = useQueryString(location);
  const selector = useCallback(
    ({ jobs }) => {
      const { keywords, type, pageSize, page } = querystring;
      const pattern = new RegExp(keywords, 'gi');
      const checkForKeywords = ({ id, title, description, company }) =>
        !keywords ||
        (!!id.match(pattern) ||
          !!title.match(pattern) ||
          !!description.match(pattern) ||
          !!company.match(pattern));
      const checkForType = ({ employment_type }) =>
        !type || employment_type === type;
      const found = jobs.filter(
        job => checkForKeywords(job) && checkForType(job)
      );
      const offset = [(page - 1) * pageSize, page * pageSize];

      return {
        results: found.slice(...offset),
        pages: Math.ceil(found.length / pageSize),
        found: found.length,
      };
    },
    [querystring]
  );
  const { results, pages, found } = useSelector(selector);
  const renderResult = useCallback(({ id, title }) => {
    const url = `/job/${id}/${slugify(title)}`.toLowerCase();

    return (
      <dl key={id}>
        <dt>
          <Link to={url}>{title}</Link>
        </dt>
        <dd>
          <Marker id={id} />
        </dd>
      </dl>
    );
  }, []);

  return (
    <Page title="Home">
      <h1>Latest jobs ({found} found)</h1>
      <Filter {...querystring} />
      {results.map(renderResult)}
      <Pagination total={pages} {...querystring} />
    </Page>
  );
};

Home.propTypes = {
  location: shape({ search: string.isRequired }).isRequired,
};

Home.defaultProps = {};

export default Home;
